html {
    box-sizing: border-box;
    height: 100vh;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}
::placeholder {
    font-size: 40px;
}
body {
    margin: 0;
    color: #333;
    font-size: 20px;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 400;
    line-height: 20px;
    display: block;
    background-color: rgb(242, 242, 242);
}


#chats {
    background-color: white;
    overflow-y: scroll;
    height: 30%;
    max-height: 80%;
    width: 100%;
    padding-top: 20px;

    padding-bottom: 10px;

    display: flex;
    flex-direction: column;

}

body>div>a {
    color: inherit;
    text-decoration: none;
}

.chat {
    text-decoration: none;
    display: flex;
    width: 100%;
    transition: background-color 0.5s;
}
.chat:hover {
    background-color: #d3d3d3;
}
.chat:active {
    background-color: #b2b2b2;
}


.chat-info{
    flex-grow: 1;
    font-size: 25px;
    display: flex;
    border: solid;
    border-color: #c9c9c9;
    border-width: 1px 0;
    width: 60%;
}
.chat-picture {
    border-radius: 50%;
    width: 60px;
    height: 60px;
    margin: 7px 7px 7px 10px;
}
.chat-text-info {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: center;
    padding-right: 10px;

}
.chat-name {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: normal;
    width: 100%;
    font-size: 20px;
    margin: 0px 10px 2px 10px;
}
.last-message {
    font-size: 18px;
    white-space: nowrap;
    line-height: normal;
    overflow-x: hidden;
    text-overflow: ellipsis;
    width: 100%;
    opacity: 60%;
    padding: 0;
    margin: 2px 10px 0px 10px;
}

.delivered {
    margin: 0 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}
.last-message-time{
    opacity: 60%;
    font-size: 15px;
}
.read-icons {
    margin-top: 10px;
    font-size: 20px;
    color: #77009C;
}


.create-chat {
  display: flex;
  justify-content: center;
  height: 70px;
  width: 70px;
  padding: 0;
  border: 0;
  transition: all 0.2s;
  font-size: 25px;
  margin: 20px;
  color: rgb(97, 79, 27);
  background-color: rgb(254, 207, 62);
  position: fixed;
  right: 0;
  bottom: 0;
  border-radius: 50%;
  box-shadow: 0 4px 10px -3px;
}
.create-chat:hover {
    animation: pulse 2s infinite;
}
.create-chat:active {
    color: #333333;
    background-color: rgb(97, 79, 27);
}
@keyframes pulse {
    0% {
        -moz-box-shadow: 0 0 0 0 rgba(204,169,44, 0.4);
        box-shadow: 0 0 0 0 rgba(204,169,44, 0.4);
    }
    70% {
        -moz-box-shadow: 0 0 0 10px rgba(204,169,44, 0);
        box-shadow: 0 0 0 10px rgba(204,169,44, 0);
    }
    100% {
        -moz-box-shadow: 0 0 0 0 rgba(204,169,44, 0);
        box-shadow: 0 0 0 0 rgba(204,169,44, 0);
    }
}

.unread{
    color: white;
    margin-top: 5px;
    font-size: 15px;
    padding: 3px 5px;
    background-color: #7A0099FF;
    border-radius: 11px;
    font-family: Helvetica, Arial, sans-serif;
}

.unread-pinged{
    animation: pulse 2s infinite;
    margin-top: 5px;
    font-size: 15px;
    padding: 3px 5px;
    background-color: rgb(254, 207, 62);
    border-radius: 11px;
    font-family: Helvetica, Arial, sans-serif;
}







.heading {
  font-size: 30px;
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  align-items: center;
  margin: 0;
}

.receiver-text {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.user-avatar {
  border-radius: 50%;
  width: 45px;
  height: 45px;
  margin: 5px;
}
#username {
  font-size: 22px;
  margin: 3px 10px;
}
#last-seen {
  font-size: 15px;
  opacity: 60%;
  padding: 0;
  margin: 3px 10px;
  line-height: 1;
}
.page-chat-list {
  width: 100%;
}

img {
  font-size: 15px;
}